<template>
  <div class="home">
    <loading :active="isLoading"></loading>
    <section ref="banner" class="banner_page position-relative mb-5">
      <div
        class="top_img parallax"
        data-depth="0.40"
        :style="{'background-image': `url(${require('@/assets/img/bg.jpg')})`}"
        ref="parallax"
      ></div>
      <div class="container h-100 position-relative z-index-1">
        <div class="row h-100 mx-0 justify-content-center align-items-center overflow-hidden">
          <div class="col-auto text-center box">
            <h2 class="page-title">作品集</h2>
            <span>works</span>
          </div>
        </div>
      </div>
    </section>
    <div class="container">
      <div class="row justify-content-md-between justify-content-center align-items-center mb-4">
        <ul class="nav nav-pills col-auto mb-3 mx-n2">
          <li class="nav-item m-2">
            <router-link to="/" class="nav-link" :class="{ active: $route.params.categoryName === '' }">
              全部
            </router-link>
          </li>
          <li class="nav-item m-2">
            <router-link to="/Business" class="nav-link" :class="{ active: $route.params.categoryName === 'Business' }">
              企業網站
            </router-link>
          </li>
          <li class="nav-item m-2">
            <router-link to="/Shop" class="nav-link" :class="{ active: $route.params.categoryName === 'Shop' }">
              電商網站
            </router-link>
          </li>
        </ul>
        <div class="col-auto mb-3">
          <div class="position-relative d-flex align-items-center">
            <input type="text" placeholder="search" v-model="search">
            <a href="#" class="clear" @click.prevent="clearSearch" v-if="search.length !== 0">X</a>
          </div>
        </div>
      </div>
      <div class="row works-list mb-4 overflow-hidden" ref="works_list">
        <template v-if="search.length === 0">
          <div class="text-end mb-3" v-if="filterSearch.length > 0" data-aos="fade-left" data-aos-duration="2500">Item: {{ filterSearch.length }}</div>
          <div class="col-lg-4 col-sm-6 mb-4" v-for="item in filterData[currentPage - 1]" :key="item" data-aos="fade-up" data-aos-duration="2000">
            <a :href="item.link" target="_blank">
              <div class="img mb-2">
                <img :src="`./img/${item.img}`" alt="" class="w-100">
                <div class="info">
                  <div class="mb-2">
                    {{item.info}}
                  </div>
                  <div class="icon">
                    <i class="bi bi-link-45deg"></i>
                  </div>
                </div>
              </div>
              <div class="title">
                <span>{{item.name}}</span>
              </div>
            </a>
          </div>
        </template>
        <template v-else>
          <div v-if="filterSearch.length === 0" class="text-center"> 無符合資料 ! </div>
          <div v-else class="text-end mb-3" data-aos="fade-left" data-aos-duration="2500">Item: {{ filterSearch.length }}</div>
          <div class="col-lg-4 col-sm-6 mb-4" v-for="item in filterSearch" :key="item" data-aos="fade-up" data-aos-duration="2000">
            <a :href="item.link" target="_blank">
              <div class="img mb-2">
                <img :src="`./img/${item.img}`" alt="" class="w-100">
                <div class="info">
                  <div class="mb-2">
                    {{item.info}}
                  </div>
                  <div class="icon">
                    <i class="bi bi-link-45deg"></i>
                  </div>
                </div>
              </div>
              <div class="title">
                <span>{{item.name}}</span>
              </div>
            </a>
          </div>
        </template>
      </div>
      <nav aria-label="Page navigation" v-if="search.length === 0">
        <ul class="pagination justify-content-center mb-5">
          <li class="page-item" :class="{'disabled' : currentPage == 1}">
            <a class="page-link" href="#" aria-label="Previous" @click.prevent="changPage(currentPage - 1)">
              <i class="bi bi-chevron-left"></i>
            </a>
          </li>
          <li class="page-item" v-for="item in filterData.length" :key="item">
            <a class="page-link" href="#"
              :class="{'active' : currentPage === item}"
              @click.prevent="changPage(item)">{{ item }}</a>
          </li>
          <li class="page-item" :class="{'disabled' : currentPage == filterData.length}">
            <a class="page-link" href="#" aria-label="Next" @click.prevent="changPage(currentPage + 1)">
              <i class="bi bi-chevron-right"></i>
            </a>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data () {
    return {
      banner: null,
      isLoading: false,
      parallaxY: null,
      list: [],
      type: '',
      currentPage: 1,
      search: ''
    }
  },
  watch: {
    filterData () {
      this.currentPage = 1
    }
  },
  computed: {
    filterData () {
      const vm = this
      if (this.$route.params.categoryName === '') {
        const newRecords = []
        this.list.forEach((item, i) => {
          if (i % 12 === 0) {
            newRecords.push([])
          }
          const page = parseInt(i / 12)
          newRecords[page].push(item)
        })
        return newRecords
      } else {
        var newList = []
        this.list.forEach(function (item) {
          if (item.type === vm.$route.params.categoryName) {
            newList.push(item)
          }
        })
        const newRecords = []
        newList.forEach((item, i) => {
          if (i % 12 === 0) {
            newRecords.push([])
          }
          const page = parseInt(i / 12)
          newRecords[page].push(item)
        })
        return newRecords
      }
    },
    filterSearch () {
      const vm = this
      return this.list.filter(item => {
        if (item.type === vm.$route.params.categoryName) {
          return item.name.toLowerCase().match(this.search.toLowerCase())
        } else if (this.$route.params.categoryName === '') {
          return item.name.toLowerCase().match(this.search.toLowerCase())
        }
      })
    }
  },
  mounted () {
    this.getList()
    this.banner = this.$refs.banner.clientHeight
    window.addEventListener('scroll', this.handleScroll)
  },
  methods: {
    getList () {
      this.isLoading = true
      const url = `${process.env.VUE_APP_API}`
      this.$http.get(url)
        .then((res) => {
          this.isLoading = false
          if (process.env.NODE_ENV === 'development') {
            this.list = res.data.sort(function (a, b) { return a.info < b.info ? 1 : -1 })
          } else {
            this.list = res.data.data.sort(function (a, b) { return a.info < b.info ? 1 : -1 })
          }
        })
    },
    changPage (page) {
      this.isLoading = true
      this.currentPage = page
      setTimeout(() => {
        document.documentElement.scrollTop = this.banner
        this.isLoading = false
      }, 500)
    },
    clearSearch () {
      this.search = ''
    },
    handleScroll () {
      const scrollPositionY = window.pageYOffset
      const parallax = document.querySelector('.parallax')
      const moveDepth = Number(parallax.dataset.depth)
      this.parallaxY = scrollPositionY * moveDepth
      if (this.parallaxY <= 60) {
        parallax.style.transform = `translateY(-${scrollPositionY * moveDepth}px)`
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.nav{
  padding-left: calc(var(--bs-gutter-x) * 0.5);
}
.clear{
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  color: #000 !important;
  z-index: 1;
}
</style>
