<template>
  <header class="fixed-top p-3" :class="{'top-hide': direction}">
    <router-link to="/">
      HUNG JI
    </router-link>
  </header>
  <div>
    <router-view/>
  </div>
  <div class="goTop btn btn-primary" :class="positionY >= 500 ? 'goTopAfter' : '' " @click="goTop">TOP</div>
</template>

<script>
export default {
  data () {
    return {
      direction: false,
      positionY: 0
    }
  },
  methods: {
    handleScroll () {
      const scrollY = window.scrollY
      if (scrollY > this.positionY) {
        this.direction = true
      } else {
        this.direction = false
      }
      this.positionY = scrollY
    },
    goTop () {
      document.documentElement.scrollTop = 0
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>
<style lang="scss">
@import "./assets/all.scss";
.goTop {
  visibility: hidden;
  transform: translateY(80px);
  position: fixed;
  bottom: 15px;
  right: 15px;
  z-index: 2;
  transition: all 1.2s cubic-bezier(.19,1,.22,1);
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  width: 53px;
  height: 53px;
  border-radius: 100%;
  padding-top: 5px;
  font-size: 14px;
  &::before{
    content: '';
    background-image: url(./assets/img/up.svg);
    background-position: center;
    background-repeat: no-repeat;
    width: 30px;
    height: 30px;
  }
  &.goTopAfter {
    visibility: visible;
    transform: translateY(0);
  }
  &:hover{
    color: #fff;
  }
}
</style>
